.main {
    &Servis {
        h3 {}

        &Items {
            padding-top: 20px;
            display: flex;
            flex-wrap: wrap;
            gap: 25px;
            &Block{
                max-width: 400px;
                flex: 1 0 200px;
                border-radius: 5px;
                box-shadow: rgba(0, 0, 0, 0.13) 0 0 35px;
                overflow: hidden;
                div{
                    padding: 10px;
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                }
                img {
                    width: 100%;
                    height: 150px;
                    object-fit: cover;
                  
                }
            }
        }
    }

}