.header {
    width: 100vw;
    min-height: 100vh;
    position: relative;

    &Background {
        position: absolute;
        z-index: 0;
        object-fit: cover;
        width: 100%;
        height: 100%;

    }

    .menu {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        z-index: 1;

        @media (max-width:700px) {
            flex-direction: column;
            gap: 20px;
        }

        h4 {
            color: white;
        }

        &Content {
            P {
                color: white;
            }

            display: flex;
            justify-content:flex-end;
            gap: 25px;
            align-items: center;
        }
    }

    
    &Title {
        padding-top: 50vh;
        position: relative;
        z-index: 1;
        color: white;
        display: flex;
        flex-direction: column;
        gap: 10px;

        @media (max-width:700px) {
            padding-top: 30vh;
        }
    }

    &Form {
        margin-top: 50px;
        position: relative;
        z-index: 1;
        color: rgba(255, 255, 255, 0.95);
        display: flex;
        flex-direction: column;
        gap: 10px;
        backdrop-filter: blur(10px);
        background-color: #ffffff0d;
        padding: 10px;
        border-radius: 5px;
        border: 1px solid rgba(255, 255, 255, 0.1);

        &Items {

            padding-top: 15px;
            display: flex;
            gap: 25px;

            @media (max-width:1200px) {
                flex-direction: column;
            }

            div {
                display: flex;
                flex: 1 0 300px;
                flex-direction: column;

                @media (max-width:1200px) {
                    flex: 1 1;
                }

                input {
                    color: rgb(255, 255, 255);
                    border-bottom: rgba(255, 255, 255, 0.25) 2px solid;
                }
            }

            button {
                color: white;
            }
        }


    }

}