// Fonts
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700;900&display=swap');
:root{
    --color-orange: #ffd600;
    --color-black: #141414;
    --color-red: #d72439;
    --color-blue: #005069;
    --color-grean: #30e581;
}
* {
    padding: 0;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    outline: none;
    text-decoration: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

h1 {
    @media (max-width: 700px) {
        font-size: calc(1.225rem + 2.3vw);
    }

    font-size: calc(1.225rem + 2.3vw);
}

h2 {
    @media (max-width: 700px) {
        font-size: calc(1.5rem + 1.1vw);
    }

    font-size: calc(1.325rem + 1.1vw);
}

h3 {
    @media (max-width: 700px) {
        font-size: calc(1.325rem + .7vw);
    }

    font-size: calc(1.325rem + .7vw);
}

h4 {
    @media (max-width: 700px) {
        font-size: calc(1.3rem + .6vw);
    }

    font-size: calc(1.3rem + .6vw);
}

h5 {
    @media (max-width: 700px) {
        font-size: calc(1.3rem + .3vw);
    }

    font-size: calc(1.3rem + .3vw);
}

h6 {
    @media (max-width: 700px) {
        font-size: calc(1.0rem + .3vw);
    }

    font-size: calc(1.0rem + .3vw);
}

p {
    font-size: 1rem;
}

button {
    font-size: 1.25rem;
    padding: 10px 15px;
    background: none;
    border: none;
    border-radius: 5px;
    transition: 0.1s ease-out all;
    cursor: pointer;

    &:hover {
        transform: scale(0.9);
    }
}

input {
    border: 2px solid;
    padding: 8px 0;
    font-size: 1rem;
    transition: 0.2s ease-out all;
    border:none;
    background: none;
    &::placeholder{
      font-weight: 400;
 
    }
}

body {
    overflow-x: hidden;
    width: 100vw;
    min-height: 100vh;
    z-index: -1;
}

html {
    overflow-x: hidden;
    width: 100vw;
    min-height: 100vh;
}




$color-orange: var(--color-orange);
$color-black: var(--color-black);
$color-red: var(--color-red);
$color-blue: var(--color-blue);
$color-grean : var(--color-grean);

.bc-orange {
    background-color: $color-orange;
}

.bc-black {
    background-color: $color-black;
}

.bc-red {
    background-color: $color-red;
}

.bc-blue {
    background-color: $color-blue;
}

.bc-grean {
    background-color: $color-grean;
}




.color-orange {
    color: $color-orange;
}

.color-black {
    color: $color-black;
}

.color-red {
    color: $color-red;
}

.color-blue {
    color: $color-blue;
}

.color-grean {
    color: $color-grean;
}



.border-color-orange {
    border-color: $color-orange;
}

.border-color-black {
    border-color: $color-black;
}

.border-color-red {
    border-color: $color-red;
}

.border-color-blue {
    border-color: $color-blue;
}

.border-color-grean {
    border-color: $color-grean;
}


.cnt {
    padding: 50px 15%;

    @media (max-width: 500px) {
        padding: 25px 5%;
    }
}
@import 'header';
@import 'main';